.profile {
  display: flex;
  flex-direction: column;
  background-color: #f9b3d1;
  justify-content: center;
  align-items: center;
  border: 1px solid #ae7d9a;
  border-radius: 12px;
  margin: 40px;
  padding: 20px;
  box-shadow: 5px 5px 15px 5px #624763;
  gap: 35px;
  transition: scale 300ms;
  width: 350px;
  overflow: hidden;
}

.profile:hover {
  scale: 1.05;
}

.description {
  margin-top: 30px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
}

.avatar {
  height: 250px;
  width: 250px;
  border-radius: 50%;
  background-color: #e6e9f0;
  border: 1px solid rgb(143, 143, 175);
  margin: 0;
  transition: scale 300ms;
}

.avatar:hover {
  scale: 1.05;
}

.name {
  font-size: 24px;
  font-weight: 700;
}

.tag {
  font-size: 20px;
  color: #0f0a1d;
}

.location {
  composes: tag;
}

.stats {
  font-size: 20px;
  display: flex;
  align-items: center;
  justify-content: space-around;
  list-style: none;
  width: 100%;
}

.statitem {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  padding: 10px;
}

.label {
  font-size: 16px;
}

.quantity {
  font-weight: 500;
}
